import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';

interface Doc {
  SystemDescription: string,
  SystemId: number,
  SystemName: string,
  SystemVisible: number,
}

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.scss']
})

export class AboutComponent implements OnInit {
  systems;
  ActiveIdx: number = 0;
  ActiveDescription: string;
  instructor: Object;
  main_desc: string = `\n<span style="font-weight: 400;"></span>\n                                                                 .\n\n<span style="font-weight: 400;"></span>\n   .\n\n<span style="font-weight: 400;"></span>\n      (   )       (    )    2020      (      )        	       . \n\n<span style="font-weight: 400;"> </span>\n                    :\n-  \n- \n- \n- \n- \n- \n\n<span style="font-weight: 400;"></span>\n " "                    .\n\n<span style="font-weight: 400;"></span>\n            :\n:   (          ....).\n:    (       .... ).\n:   (        ...).\n:   (         ... ).\n
  `;
  about: SafeHtml;

  constructor(private http: HttpClient, private router: Router, private sanitizer: DomSanitizer) { 
    this.about = this.sanitizer.bypassSecurityTrustHtml(this.main_desc);
  }

  async ngOnInit() {
    this.systems = await this.http.get('https://sheikhalamoud.org/apis/systems/').toPromise();
    this.systems.map(sys => sys.description = sys.description.replace(/(?:\\[rn]|[\r\n]+)+/g, "\n\n").replace(/\\"/g, '"'))
    this.ActiveDescription = this.systems[0]['description'];
    localStorage.setItem('payload', JSON.stringify({params: {system_ids: [this.systems[0]['uid']]}}));
  }

  changeTo(idx: number){
    this.ActiveIdx = idx;
    this.ActiveDescription = this.systems[idx]['description'];

    localStorage.setItem('payload', JSON.stringify({params: {system_ids: [this.systems[idx]['uid']]}}));
  }

  clearFiltersAndNavigate() {
    localStorage.setItem('selectedSystems', JSON.stringify([this.systems[this.ActiveIdx]['uid']]));
    localStorage.setItem('selectedLocations', JSON.stringify([0]));
    localStorage.setItem('selectedDays', JSON.stringify(["all"]));
    this.router.navigate(['schedules']);
  }
}
